import equal from 'fast-deep-equal';
import { Controller } from '@ali/4ever-cangjie';
import { CommentPluginConfigs } from '../types';
import { marksToDecorations } from '../utils/marksToDecorations';

export default (configs: CommentPluginConfigs | undefined) => {
  return function onChange(controller: Controller, next) {
    if (configs?.isDisabled && configs.isDisabled()) {
      return next();
    }
    const decorations = marksToDecorations(controller);
    if (!equal(decorations, controller.value.decorations)) {
      controller.setDecorations(decorations);
    }
    return next();
  };
};
